import { useTransition, animated } from '@react-spring/web';

const HeaderText = ({texts}) => {

  const transitions = useTransition(texts,
    {
      keys: text => text,
      from: { opacity: 0},
      enter: { opacity: 1 },
      leave: { opacity: 0},
      exitBeforeEnter: true,
    }
  );
  return transitions(({opacity}, text) => (
    <animated.div style={{
      opacity: opacity,
      position: 'absolute',
      top: '0px',
      left: '0px',
      bottom: '0px',
      right: '0px',
      backgroundColor: '#fff',
    }}>{text}</animated.div>
  ));
}

export default HeaderText;